.back-button {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50px;
  left: 50px;
  z-index: 10;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: #db5461 1px solid;
  overflow: hidden;
  transition: background 0.3s ease;
}
@media only screen and (max-width: 450px) {
  .back-button {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 30px;
    left: 30px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    border: #db5461 1px solid;
    overflow: hidden;
    transition: background 0.3s ease;
  }
}
.back-button.back .arrow-wrap {
  left: -50%;
}
.back-button:hover {
  background: #db5461;
}
.back-button:hover .arrow-wrap span {
  background: #fff;
}
.back-button .arrow-wrap {
  display: block;
  position: absolute;
  height: 70%;
  width: 70%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: left 0.3s ease;
}
.back-button .arrow-wrap span {
  height: 1px;
  left: 0;
  top: 50%;
  background: #db5461;
  position: absolute;
  display: block;
  transition: background 0.3s ease;
}
.back-button .arrow-wrap .arrow-part-1 {
  width: 100%;
  transform: translate(0, -50%);
}
.back-button .arrow-wrap .arrow-part-2 {
  width: 60%;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}
.back-button .arrow-wrap .arrow-part-3 {
  width: 60%;
  transform: rotate(45deg);
  transform-origin: 0 0;
}
