.mb20 {
  margin-bottom: 20px;
}
.wrapper {
  position: relative;
  clear: both;
  margin: 0 auto 10px auto;
  width: 100%;
  overflow: hidden;
}

.top .title {
  width: 700px;
  margin: 38px auto 0 auto;
}

.title h1 {
  font-size: 24px;
  color: #fff;
  font-weight: 500;
}

.content {
  padding-bottom: 20px;
}

.content .card.first {
  margin-top: -80px;
}

@media only screen and (max-width: 650px) {
  .card {
    position: relative;
    background: #fff;
    padding: 15px;
    overflow: hidden;
    margin: 20px auto 0 auto;
    text-overflow: ellipsis;
  }
  .top {
    background: #ff560124;
    height: 160px;
  }
  .blogList p {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
  .card h2 {
    color: #db5461;
    text-decoration: none;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
  .blogPage {
    height: auto;
    width: auto;
    margin: 0px;
  }
  .card h1 {
    color: #db5461;
    text-decoration: none;
    font-size: 25px;
    margin: 0;
  }
}

@media only screen and (min-width: 650px) {
  .card {
    position: relative;
    background: #fff;
    padding: 40px;
    width: 650px;
    margin: 20px auto 0 auto;
    box-shadow: 0 2px 4px rgba(100, 100, 100, 0.71);
  }
  .blogPage {
    height: auto;
    width: auto;
    margin: 40px;
  }
  .top {
    background: #ff560124;
    height: 180px;
  }
  .card h2 {
    color: #db5461;
    text-decoration: none;
  }

  .card h1 {
    color: #db5461;
    text-decoration: none;
    font-size: 35px;
    font-weight: 500;
    margin: 0;
  }
}
.card h2 {
  font-weight: 500;
  margin: 0;
}
.card .date {
  color: #9e9e9e;
  margin-bottom: 10px;
  font-size: 14px;
}

.card .text {
  color: #212121;
  margin-top: 20px;
  font-size: 15px;
  line-height: 22px;
}

pre {
  background: #333;
  white-space: pre;
  word-wrap: break-word;
  overflow: auto;
}

pre.code {
  margin: 20px 25px;
  border-radius: 4px;
  border: 1px solid #292929;
  position: relative;
}

pre.code label {
  font-family: sans-serif;
  font-weight: bold;
  font-size: 13px;
  color: #ddd;
  position: absolute;
  left: 1px;
  top: 15px;
  text-align: center;
  width: 60px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  pointer-events: none;
}

pre.code code {
  display: block;
  padding: 15px 16px 14px;
  border-left: 1px solid #555;
  overflow-x: auto;
  font-size: 13px;
  line-height: 19px;
  color: #ddd;
}

pre::after {
  padding: 0;
  width: auto;
  height: auto;
  position: absolute;
  right: 18px;
  top: 14px;
  font-size: 12px;
  color: #ddd;
  line-height: 20px;
  overflow: hidden;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transition: all 0.3s ease;
}

pre:hover::after {
  opacity: 0;
  visibility: visible;
}

pre.code-css code {
  color: #91a7ff;
}

pre.code-html code {
  color: #aed581;
}

pre.code-javascript code {
  color: #f1f1f1;
}

pre.code-jquery code {
  color: #4dd0e1;
}
.highlight {
  background-color: #ffeb004f;
}
.dib {
  display: inline-block;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
